const util = require('./helper.js');

/** 0 */
function simpleDate(date) {
    let arr = date.split('-');
    if (arr.length < 3) {
        return date;
    }
    let month = arr[1];
    if (month.indexOf('0') == 0) {
        month = month.replace('0', '');
    }
    let day = arr[2];
    if (day.indexOf('0') == 0) {
        day = day.replace('0', '');
    }
    return arr[0] + '-' + month + '-' + day;
}

/**  */
function fmtDateCHN(date, fmt = 'Y-M-D') {
    if (!date) {
        return '';
    }
    if (fmt == 'hh:mm' && date.includes(':')) {
        if (date.includes(' ')) {
            date = date.split(' ')[1];
        }
        let arr = date.split(':');
        return Number(arr[0]) + '' + arr[1] + '';
    } else if (fmt == 'Y-M-D hh:mm') {
        let arr = date.split(' ');
        if (arr.length != 2) {
            return date;
        }
        return fmtDateCHN(arr[0], 'Y-M-D') + fmtDateCHN(arr[1], 'hh:mm');
    } else if (fmt == 'M-D hh:mm') {
        let arr = date.split(' ');
        if (arr.length != 2) {
            return date;
        }
        return fmtDateCHN(arr[0], 'M-D') + ' ' + fmtDateCHN(arr[1], 'hh:mm');
    } else {
        if (date.includes(' ')) {
            date = date.split(' ')[0];
        }
        let arr = date.split('-');
        if (fmt == 'Y-M')
            //
            return arr[0] + '' + Number(arr[1]) + '';
        else if (fmt == 'M-D')
            //
            return arr[1] + '' + Number(arr[2]) + '';
        else if (fmt == 'Y') {
            //
            return arr[0] + '';
        } else {
            return arr[0] + '' + Number(arr[1]) + '' + Number(arr[2]) + '';
        }
    }
}

/**
 * 
 * @param {*} unixtime  
 * @param {*} format  Y-M-D h:m:s
 * @param {*} diff   
 */
function timestamp2Time(unixtime, format = 'Y-M-D h:m:s', diff = 0) {
    let formateArr = ['Y', 'M', 'D', 'h', 'm', 's'];
    let returnArr = [];
    let date = new Date(unixtime + diff);
    returnArr.push(date.getFullYear());
    returnArr.push(formatNumber(date.getMonth() + 1));
    returnArr.push(formatNumber(date.getDate()));
    returnArr.push(formatNumber(date.getHours()));
    returnArr.push(formatNumber(date.getMinutes()));
    returnArr.push(formatNumber(date.getSeconds()));
    for (let i in returnArr) {
        format = format.replace(formateArr[i], returnArr[i]);
    }
    return format;
}
function timestame2Ago(dateTimeStamp, fmt = 'Y-M-D', diff = 0) {
    //dateTimeStamp100013
    let minute = 60000; //
    let hour = minute * 60;
    let day = hour * 24;
    let week = day * 7;
    let month = day * 30;
    let now = new Date().getTime(); //

    let diffValue = now - dateTimeStamp; //

    if (diffValue < 0) {
        return;
    }
    let minC = diffValue / minute; //
    let hourC = diffValue / hour;
    let dayC = diffValue / day;
    let result = '';
    let weekC = diffValue / week;
    let monthC = diffValue / month;
    if (monthC >= 1 && monthC <= 3) {
        result = ' ' + parseInt(monthC) + '';
    } else if (weekC >= 1 && weekC <= 3) {
        result = ' ' + parseInt(weekC) + '';
    } else if (dayC >= 1 && dayC <= 6) {
        result = ' ' + parseInt(dayC) + '';
    } else if (hourC >= 1 && hourC <= 23) {
        result = ' ' + parseInt(hourC) + '';
    } else if (minC >= 1 && minC <= 59) {
        result = ' ' + parseInt(minC) + '';
    } else if (diffValue >= 0 && diffValue <= minute) {
        result = '';
    } else {
        result = timestamp2Time(dateTimeStamp, fmt, diff);
    }
    return result;
}
function formatNumber(n) {
    n = n.toString();
    return n[1] ? n : '0' + n;
}

/**
 * 
 * @param {*} date   Y-M-D h:m:s / Y-M-D
 */
function time2Timestamp(date) {
    if (date.length < 10) {
        let arr = date.split('-');
        if (arr[1].length == 1) {
            arr[1] = '0' + arr[1];
        }
        if (arr[2].length == 1) {
            arr[2] = '0' + arr[2];
        }
        date = arr[0] + '-' + arr[1] + '-' + arr[2];
    }
    if (date.length == 10) {
        date = date + ' 00:00:00';
    }
    let d = new Date(date.replace(/-/g, '/'));
    return d.getTime();
}

/**
 *  /Y-M-D h:m:s
 * @param {*}  Y-M-D h:m:s
 * @param {int}  ()
 */
function time(fmt, step = 0) {
    let t = 0;
    if (util.isDefined(fmt)) {
        let t = new Date().getTime() + step * 1000;
        return timestamp2Time(t, fmt);
    }
    return new Date().getTime() + t * 1000;
}

// 0
function getDayFirstTimestamp(timestamp) {
    if (!timestamp) {
        timestamp = time();
    }
    return time2Timestamp(timestamp2Time(timestamp, 'Y-M-D'));
}

/**
 *  1996-06-08
 * @param {*} birth
 */
function getAge(birth, isMonth = false) {
    var returnAge = '';
    var mouthAge = '';
    var arr = birth.split('-');
    var birthYear = arr[0];
    var birthMonth = arr[1];
    var birthDay = arr[2];
    var d = new Date();
    var nowYear = d.getFullYear();
    var nowMonth = d.getMonth() + 1;
    var nowDay = d.getDate();
    if (nowYear == birthYear) {
        // returnAge = 0; // 0
        var monthDiff = nowMonth - birthMonth; //
        if (monthDiff < 0) {
        } else {
            mouthAge = monthDiff + '';
        }
    } else {
        var ageDiff = nowYear - birthYear; //
        if (ageDiff > 0) {
            if (nowMonth == birthMonth) {
                var dayDiff = nowDay - birthDay; //
                if (dayDiff < 0) {
                    returnAge = ageDiff - 1 + '';
                } else {
                    returnAge = ageDiff + '';
                }
            } else {
                var monthDiff = nowMonth - birthMonth; //
                if (monthDiff < 0) {
                    returnAge = ageDiff - 1 + '';
                } else {
                    mouthAge = monthDiff + '';
                    returnAge = ageDiff + '';
                }
            }
        } else {
            returnAge = -1; //-1  
        }
    }

    if (isMonth) {
        return returnAge + mouthAge; //+
    } else {
        return returnAge;
    }
}

/**
 * 
 * @param {*} day   2013-03-10
 */
function week(day) {
    let arys1 = new Array();
    arys1 = day.split('-');
    let ssdate = new Date(arys1[0], parseInt(arys1[1] - 1), arys1[2]);
    let week1 = String(ssdate.getDay()).replace('0', '').replace('1', '').replace('2', '').replace('3', '').replace('4', '').replace('5', '').replace('6', ''); //
    return '' + week1; //
}

/**  */
function getMonthFirstTimestamp(timestamp) {
    let inDate = new Date(timestamp);
    let year = inDate.getFullYear();
    let month = inDate.getMonth();
    return new Date(year, month, 1).getTime();
}

/**  */
function getMonthLastTimestamp(timestamp) {
    let inDate = new Date(timestamp);
    let year = inDate.getFullYear();
    let month = inDate.getMonth();
    return new Date(year, month + 1, 1).getTime() - 1;
}

// 
function getNowMinTimestamp() {
    let min = time('Y-M-D h:m') + ':00';
    let timestamp = time2Timestamp(min);
    return {
        min,
        timestamp
    };
}

//  =yyyy-mm-dd
function getFirstOfWeek(date) {
    let now = new Date(date);
    let nowTime = now.getTime();
    let day = now.getDay();
    if (day == 0) {
        day = 7;
    }
    let oneDayTime = 86400 * 1000;
    let mondayTime = nowTime - (day - 1) * oneDayTime;
    return timestamp2Time(mondayTime, 'Y-M-D');
}

//  =yyyy-mm-dd
function getLastOfWeek(date) {
    let now = new Date(date);
    let nowTime = now.getTime();
    let day = now.getDay();
    if (day == 0) {
        day = 7;
    }
    let oneDayTime = 86400 * 1000;
    let sundayTime = nowTime + (7 - day) * oneDayTime;
    return timestamp2Time(sundayTime, 'Y-M-D');
}

//  =yyyy-mm-dd
function getFirstOfMonth(date) {
    let arr = date.split('-');
    return arr[0] + '-' + arr[1] + '-01';
}

//  =yyyy-mm-dd
function getLastOfMonth(date) {
    let now = new Date(date);
    let y = now.getFullYear();
    let m = now.getMonth();
    let lastDay = new Date(y, m + 1, 0).getTime();
    return timestamp2Time(lastDay, 'Y-M-D');
}

/**
 *  Y-M-D/Y-M-D h:m:s
 * @param {*} datetimeTo
 * @param {*} flag 1= -1=
 */
function getTimeLeft(datetimeTo, flag = 1) {
    let time1 = datetimeTo;
    if (String(datetimeTo).includes('-')) {
        datetimeTo = String(datetimeTo);
        if (!datetimeTo.includes(':')) {
            datetimeTo += ' 00:00:00';
        }
        time1 = new Date(datetimeTo).getTime();
    }
    let time2 = new Date().getTime();
    let mss = time1 - time2;

    // 
    let days = parseInt(mss / (3600000 * 24));
    let hours = parseInt((mss % (3600000 * 24)) / 3600000);
    let minutes = parseInt((mss % 3600000) / 60000);
    let seconds = parseInt((mss % 60000) / 1000);
    return [flag * days, flag * hours, flag * minutes, flag * seconds];
}
module.exports = {
    fmtDateCHN,
    simpleDate,
    getTimeLeft,
    getNowMinTimestamp,
    getMonthFirstTimestamp,
    getMonthLastTimestamp,
    getDayFirstTimestamp,
    timestamp2Time,
    timestame2Ago,
    time2Timestamp,
    time,
    getAge,
    week,
    //

    getFirstOfWeek,
    getLastOfWeek,
    getFirstOfMonth,
    getLastOfMonth
};
