function getWritePhotosAlbum(callback) {
    uni.getSetting({
        success: (res) => {
            console.log('res=', res);
            if (res.authSetting['scope.writePhotosAlbum']) {
                console.log('true');
                if (callback) {
                    callback();
                }
            } else if (res.authSetting['scope.writePhotosAlbum'] === undefined) {
                uni.showModal({
                    title: '',
                    content: '',
                    success: (res) => {
                        if (res.confirm) {
                            uni.authorize({
                                scope: 'scope.writePhotosAlbum',
                                success: (res) => {
                                    if (callback) {
                                        callback();
                                    }
                                    console.log('', res);
                                },
                                fail: (res) => {
                                    console.log(' fail=', res);
                                    uni.showToast({
                                        title: '',
                                        icon: 'none'
                                    });
                                }
                            });
                        } else {
                            console.log('');
                        }
                    }
                });
            } else {
                uni.showModal({
                    title: '',
                    content: '',
                    success: (res) => {
                        if (res.confirm) {
                            uni.openSetting({
                                success: (res) => {
                                    uni.showToast({
                                        icon: 'none',
                                        title: ''
                                    });
                                    if (res.authSetting['scope.writePhotosAlbum']) {
                                        console.log('false success res=', res);
                                        if (callback) {
                                            callback();
                                        }
                                    } else {
                                        uni.showToast({
                                            title: '',
                                            icon: 'none'
                                        });
                                    }
                                },
                                fail: (res) => {
                                    console.log('false file res=', res);
                                }
                            });
                        } else {
                            uni.showToast({
                                title: '',
                                icon: 'none'
                            });
                        }
                    }
                });
            }
        }
    });
}
module.exports = {
    getWritePhotosAlbum
};
