function scrollTop(e, that) {
    if (e.scrollTop > 100) {
        that.setData({
            topShow: true
        });
    } else {
        that.setData({
            topShow: false
        });
    }
}

/**
 * 
 * @param {*} that
 * @param {*} idx  
 * @param {*} imgListName  
 */
function delImage(that, idx, imgListName = 'imgList') {
    let callback = function () {
        that.data[imgListName].splice(idx, 1);
        that.setData({
            [imgListName]: that.data[imgListName]
        });
    };
    showConfirm('', callback);
}

