// 
function formClearFocus(that) {
    let data = that.data;
    let focus = {};
    for (let key in data) {
        if (key.startsWith('form') && !key.endsWith('Focus')) {
            focus[key + 'Focus'] = null;
        }
    }
    that.setData({
        ...focus
    });
}

// 
function formHint(that, formName, hint) {
    that.setData({
        [formName + 'Focus']: hint
    });
    return showModal(hint);
}

// 
function showConfirm(title = '', yes, no) {
    return uni.showModal({
        title: '',
        content: title,
        cancelText: '',
        confirmText: '',
        success: (res) => {
            if (res.confirm) {
                if (yes) {
                    yes();
                }
            } else if (res.cancel) {
                if (no) {
                    no();
                }
            }
        }
    });
}
function showModal(content, title = '', callback = null, confirmText = null) {
    return uni.showModal({
        title,
        content: content,
        confirmText: confirmText || '',
        showCancel: false,
        success(res) {
            if (callback) {
                callback();
            }
        }
    });
}

