// 
function anchor(id, that) {
    try {
        let query = uni.createSelectorQuery().in(that);
        query.selectViewport().scrollOffset();
        //#comm id
        query.select('#' + id).boundingClientRect();
        query.exec(function (res) {
            if (!res || res.length != 2 || !res[0] || !res[1]) {
                return;
            }
            //id

            let miss = res[0].scrollTop + res[1].top - 10;
            uni.pageScrollTo({
                scrollTop: miss,
                duration: 300
            });
        });
    } catch (err) {
        console.log('CatchClause', err);
        console.log('CatchClause', err);
        console.error(err);
    }
}

// /
function url(e, that) {
    let url = e.currentTarget.dataset.url;
    let type = e.currentTarget.dataset.type;
    if (!type) {
        type = 'url';
    }
    switch (type) {
        case 'picker': {
            //picker trigger
            let item = e.currentTarget.dataset.item;
            that.setData({
                [item]: e.detail
            });
            break;
        }
        case 'top': {
            top();
            break;
        }
        case 'mini': {
            uni.navigateToMiniProgram({
                appId: e.currentTarget.dataset.app,
                path: url,
                envVersion: 'release'
            });
            break;
        }
        case 'redirect': {
            if (!url) {
                return;
            }
            uni.redirectTo({
                url
            });
            break;
        }
        case 'reLaunch':
        case 'relaunch': {
            if (!url) {
                return;
            }
            uni.reLaunch({
                url
            });
            break;
        }
        case 'copy': {
            uni.setClipboardData({
                data: url,
                success(res) {
                    uni.getClipboardData({
                        success(res) {
                            showNoneToast('');
                        }
                    });
                }
            });
            break;
        }
        case 'hint': {
            if (!url) {
                return;
            }
            showModal(url);
            break;
        }
        case 'switch': {
            if (!url) {
                return;
            }
            uni.switchTab({
                url
            });
            break;
        }
        case 'back': {
            uni.navigateBack();
            break;
        }
        case 'toURL': {
            toURL(url);
            break;
        }
        case 'phone': {
            uni.makePhoneCall({
                phoneNumber: url
            });
            break;
        }
        case 'anchor': {
            anchor(url, that);
            break;
        }
        case 'saveimg':
        case 'saveimage': {
            let callback = function () {
                uni.saveImageToPhotosAlbum({
                    //
                    filePath: url,
                    //
                    success: function (res) {
                        uni.showToast({
                            title: e.currentTarget.dataset.hint || '',
                            icon: 'none',
                            duration: 2000
                        });
                    },
                    fail: function (err) {
                        console.log(err);
                    }
                });
            };
            picHelper.getWritePhotosAlbum(callback);
            break;
        }
        case 'bool': //
        {
            that.setData({
                [url]: !that.data[url]
            });
            break;
        }
        case 'img':
        case 'image': {
            if (url.indexOf('qlogo') > -1) {
                //
                url = url.replace('/132', '/0');
            }
            let urls = [url];
            if (helper.isDefined(e.currentTarget.dataset.imgs)) {
                urls = e.currentTarget.dataset.imgs;
            }
            uni.previewImage({
                current: url,
                // http
                urls
            });
            break;
        }
        default:
            if (!url) {
                return;
            }
            uni.navigateTo({
                url
            });
    }
}
function getOptions(that, options, idName = 'id') {
    let id = options[idName];
    if (!id) {
        id = options['scene'];
    } // 

    if (!id) {
        return false;
    }
    that.setData({
        [idName]: id
    });
    return true;
}

// 
function hint(msg, type = 'redirect') {
    if (type == 'reLaunch') {
        uni.reLaunch({
            url: fmtURLByPID('/pages/public/hint?type=9&msg=' + encodeURIComponent(msg))
        });
    } else {
        uni.redirectTo({
            url: fmtURLByPID('/pages/public/hint?type=9&msg=' + encodeURIComponent(msg))
        });
    }
}

//  delta=1
function toURL(url) {
    let pages = getCurrentPages();
    for (let k = 0; k < pages.length; k++) {
        if (pages[k].route.includes(url)) {
            uni.navigateBack({
                delta: pages.length - k - 1
            });
            return;
        }
    }
    uni.redirectTo({
        url
    });
}

/** ListTouch */
function listTouchStart(e, that) {
    that.setData({
        touchX: e.touches[0].pageX
    });
}

/** ListTouch */
function listTouchMove(e, that, precision = 50) {
    if (that.data.touchX - e.touches[0].pageX > precision) {
        that.setData({
            touchDirection: 'left'
        });
    } else if (that.data.touchX - e.touches[0].pageX < -precision) {
        that.setData({
            touchDirection: 'right'
        });
    }
}

/** ListTouch */
function listTouchEnd(e, that) {
    if (that.data.touchDirection == 'left') {
        that.setData({
            touchCur: e.currentTarget.dataset.idx
        });
    } else {
        that.setData({
            touchCur: null
        });
    }
    that.setData({
        touchDirection: null
    });
}

/**
 * 
 * @param {*} e
 * @param {*} key 
 * @param {*} val  
 * @param {*} def  (int,str,float)
 */
function queryMulti(that, e, key, val, def) {
    key = helper.isDefined(key) ? key : dataset(e, 'key');
    val = helper.isDefined(val) ? val : dataset(e, 'val');
    def = helper.isDefined(def) ? def : dataset(e, 'def');

    // 
    if (def == 'int') {
        val = parseInt(val);
    } else if (def == 'float') {
        val = parseFloat(val);
    } else if (def == 'str') {
        val = val.toString();
    }
    let _params = that.data._params;
    _params.query[key] = val;
    that.setData({
        _params
    });
}

/**
 * 
 * @param {*} key
 * @param {*} that
 * @param {*} listKey  KEY
 */
function cacheListExist(key, that, listKey = 'list') {
    key = key.toUpperCase();
    if (setting.CACHE_IS_LIST) {
        return cacheHelper.get(key + '_LIST') && that.data && that.data[listKey];
    } else {
        return false;
    }
}
function cacheListRemove(key) {
    key = key.toUpperCase();
    if (setting.CACHE_IS_LIST) {
        cacheHelper.remove(key + '_LIST');
    }
}
function cacheListSet(key, time = setting.CACHE_LIST_TIME) {
    key = key.toUpperCase();
    if (setting.CACHE_IS_LIST) {
        cacheHelper.set(key + '_LIST', 'TRUE', time);
    }
}

// 
function makeListMenu(arr, label, mark) {
    let ret = [];
    ret.push({
        label,
        type: mark,
        value: ''
    });
    for (let k = 0; k < arr.length; k++) {
        ret.push({
            label: arr[k],
            type: mark,
            value: arr[k]
        });
    }
    return ret;
}
