function getAuth(auth, authName, callback) {
    auth = 'scope.' + auth;
    uni.getSetting({
        success: (res) => {
            console.log(res);
            if (res.authSetting[auth]) {
                console.log('true');
                if (callback) {
                    callback();
                }
            } else if (res.authSetting[auth] === undefined) {
                // 
                uni.showModal({
                    title: '',
                    mask: true,
                    content: '' + authName + '',
                    success: (res) => {
                        if (res.confirm) {
                            uni.authorize({
                                scope: auth,
                                success: (res) => {
                                    console.log('', res);
                                    if (callback) {
                                        callback();
                                    }
                                },
                                fail: (res) => {
                                    console.log(' fail=', res);
                                    uni.showToast({
                                        mask: true,
                                        title: '' + authName,
                                        icon: 'none'
                                    });
                                }
                            });
                        } else {
                            uni.showToast({
                                mask: true,
                                title: '' + authName,
                                icon: 'none'
                            });
                        }
                    }
                });
            } else {
                // 
                uni.showModal({
                    title: '',
                    content: '' + authName + '',
                    success: (res) => {
                        if (res.confirm) {
                            uni.openSetting({
                                success: (res) => {
                                    uni.showToast({
                                        mask: true,
                                        icon: 'none',
                                        title: '' + authName
                                    });
                                    if (res.authSetting[auth]) {
                                        console.log('false success res=', res);
                                        if (callback) {
                                            callback();
                                        }
                                    } else {
                                        uni.showToast({
                                            mask: true,
                                            title: '' + authName + '',
                                            icon: 'none'
                                        });
                                    }
                                },
                                fail: (res) => {
                                    console.log('false file res=', res);
                                }
                            });
                        } else {
                            uni.showToast({
                                mask: true,
                                title: '' + authName,
                                icon: 'none'
                            });
                        }
                    }
                });
            }
        }
    });
}
module.exports = {
    getAuth
};
